<template>
  <v-container>
    <v-row>
      <v-col cols="1">
        <div class="text-center">
        <small>Overall</small><br/>
        </div>
        <v-progress-circular
        :size="70"
        :width="15"
        :value="value"
        color="teal"
        >
        {{ value }}%
        </v-progress-circular>
        <div class="text-center">
        <small>Total: {{ totalHours }}</small><br/>
        <small>Used: {{ hours }}</small>
        </div>
      </v-col>
      <v-col cols="11">

        <v-card v-for="task in tasks" :key="task.id" class="mt-12">
          <v-row>
            <h3 class="mx-8 mt-2">{{ task.project_name }}</h3>
          </v-row>
          <v-row>
            <small class="mx-8">Horas Alocadas Mês: {{ task.total_hours }}</small>
          </v-row>
          <v-row>
            <small class="mx-8">Horas Utilizadas Mês: {{ task.hours }}</small>
          </v-row>
          <v-row>
            <v-col class="px-12">
            <v-progress-linear
              color="amber"
              height="25"
              :value="Math.ceil( (task.hours / task.total_hours) * 100 )"
            >
              <strong>{{ Math.ceil( (task.hours / task.total_hours) * 100 ) }}%</strong>
            </v-progress-linear>
            </v-col>
          </v-row>
          <v-row>
            <v-stepper :value="task.current_step" class="mx-auto ma-2 text-caption">
            <v-stepper-header>
              <v-stepper-step v-for="step in task.steps" :key="step.id" :step="step.id" :complete="step.completed">
                {{ step.name }}
              </v-stepper-step>
            </v-stepper-header>
          </v-stepper>
          </v-row>
        </v-card>
      </v-col>

    </v-row>
  </v-container>
  
</template>

<script>
import tasksData from "../assets/tasks.json";

  export default {
    name: 'TaskManager',

    data: () => ({
      value: 0,
      tasks: tasksData.tasks,
      totalHours: 0,
      hours: 0
    }),
    created: function () {
      var i;
      for (i=0; i < this.tasks.length; i++) {
        this.hours += this.tasks[i].hours;
        this.totalHours += this.tasks[i].total_hours
        console.log(this.tasks[i].hours);
        console.log(this.totalHours);
        console.log(this.hours);
      }
      this.value = Math.round((this.hours/this.totalHours)*100);
      console.log(this.value)
    }
  }
</script>
